"use strict";
var request = require("superagent");
var Biff = require("../../biff");
var ActionTypes = require("../consts/actionTypes");
var stubData = require("./submissionsStubData");

function getSubmissions(filter) {
    var self = this;
    self.dispatch({
        actionType: ActionTypes.requestSubmissions,
        data: filter
    });

    var result = [];
    if (filter === 'popular')
        result = stubData.popular();
    if (filter == 'newest')
        result = stubData.newest();

    setTimeout(function () {
        self.dispatch({
            actionType: ActionTypes.requestSubmissionsOk,
            data: result
        });
    }, 250);

    /*
     request
     .get("http://localhost:8080/submission/" + filter)
     .set("Accept", "application/json")
     .end(function (err, res) {
     if (err) {
     self.dispatch({
     actionType: ActionTypes.requestSubmissionsFailed,
     data: err
     });
     }

     self.dispatch({
     actionType: ActionTypes.requestSubmissionsOk,
     data: res.body
     });
     });
     */
}

module.exports = Biff.createActions({
    getSubmissions: getSubmissions
});



